body {
  background-color: #1a1a1a;
  color: #e6e6e6;
  width: 100vw;
}

#main {
  width: fit-content;
  margin: 0 auto;
}

.table_container {
  position: relative;
  border-radius: 12px;
  overflow: auto;
  width: fit-content;
  border: 2px solid #404040;
}

.table_header {
  position: sticky;
  top: 0;
  left: 0;
  background-color: #262626;
}

table {
  border-spacing: 0px;
  background-color: #262626;
}

th {
  cursor: pointer;
  user-select: none;
}

tr:not(:last-child) td,
tr:not(:last-child) th {
  border-bottom: 2px solid #404040;
}

td,
th {
  text-align: left;
  margin: 0;
  padding: 8px 12px;
}

td:not(:last-child),
th:not(:last-child) {
  border-right: 2px solid #404040;
}

tr:nth-child(even) {
  background-color: #333333;
}

.selected {
  background-color: #666666;
}
